.news {
  padding: 65px 0;
  &__top {
    display: inline-flex;
    align-items: center;
  }
  &__select:not(:first-child) {
    margin-left: 24px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
    margin-top: 32px;
  }
  .section-5_col {
    width: 100%;
    padding: 0;
  }
  .section-5_button {
    &:after {
      display: none;
    }
  }
  &__btn {
    width: 266px;
    height: 65px;
    margin-top: 40px;
    span {
      font-size: 21px;
      line-height: 120%;
      font-weight: 700;
    }
  }
}

@media screen and (max-width: 1200px) {
  .news {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    }
  }
}

@media screen and (max-width: 767px) {
  .news {
    padding: 30px 0;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
    }
    .section-5_col {
      margin: 0;
    }
    &__btn {
      width: 200px;
      height: 50px;
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .news {
    &__select {
      &:not(:first-child) {
        margin-left: 16px;
      }
    }
    .nice-select {
      width: 150px;
      height: 35px;
      padding: 3px 20px 3px 12px;
      &:after {
        right: 5px;
        width: 12px;
        height: 6px;
        background-size: contain;
      }
      &:before {
        display: none;
      }
      .current {
        font-size: 12px;
      }
      .list li {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .news {
    &__top {
      flex-direction: column;
    }
    &__select {
      &:not(:first-child) {
        margin: 16px 0 0 0;
      }
    }
  }
}