.reactor {
  padding: 65px 0 40px 0;
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 32px;
  }
  &__img {
    width: 730px;
    margin-right: 16px;
    img {
      width: 100%;
      border-radius: 12px;
    }
  }
  &__desc {
    width: calc(100% - 746px);
    background: #F0F7FC;
    border-radius: 12px;
    padding: 60px 32px;
  }
  &__text {
    p {
      span {
        color: #025EA1;
      }
    }
    p:not(:first-child) {
      margin-top: 16px;
    }
  }
}
.characteristic {
  padding: 40px 0;
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 32px;
  }
  &__left {
    width: calc(100% - 661px);
  }
  &__right {
    width: 645px;
    margin-left: 16px;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
  }
  &__item {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 16px / 2));
    gap: 16px;
    text-align: center;
    border-radius: 8px;
    padding: 10px;
    &:nth-child(1) {
      background: #025EA1 !important;
      margin-bottom: 24px;
    }
    &:nth-child(odd) {
      background: #ffffff;
    }
    &:nth-child(even) {
      background: #F0F7FC;
    }
    &-title {
      color: #ffffff;
      text-transform: uppercase;
    }
  }
}
.characteristic2 {
  padding: 40px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 450px;
    margin-right: 24px;
  }
  &__right {
    width: calc(100% - 474px);
    display: flex;
  }
  &__images {
    display: flex;
  }
  &__img {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &:not(:first-child) {
      margin-left: 15px;
    }
    &:nth-child(even) {
      img {
        order: -1;
        margin-top: 70px;
      }
      span {
        margin: 24px 0 0 0;
      }
    }
    img {
      max-width: 57px;
    }
    span {
      margin-bottom: 24px;
    }
  }
  &__img2 {
    width: calc(100% - 445px);
    img {
      width: 100%;
    }
  }
  &__group {
    width: 405px;
    margin-left: 40px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 19px;
    margin-top: 19px;
  }
  &__item {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 35px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      margin-right: 48px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -24px;
        transform: translate(0, -50%);
        width: 6px;
        height: 2px;
        background: #000000;
      }
      img {
        width: 100%;
      }
    }
  }
}
.table {
  padding: 40px 0;
  &__inner {
    overflow-y: auto;
    &::-webkit-scrollbar {
      height: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #000000;
      border-radius: 3px;
    }
  }
  table {
    min-width: 1440px;
    width: 100%;
    text-align: center;
    thead {
      background: #025EA1;
      padding: 8px 24px;
      tr th {
        color: #ffffff;
        text-transform: uppercase;
        padding: 8px 25px;
        &:first-child {
          border-radius: 8px 0 0 8px;
        }
        &:last-child {
          border-radius: 0 8px 8px 0;
        }
      }
    }
    tbody {
      tr:nth-child(1) {
        height: 40px;
      }
      tr:nth-child(odd) {
        background: #ffffff;
      }
      tr:nth-child(even) {
        background: #F0F7FC;
      }
      tr td {
        padding: 5px 25px;
        &:first-child {
          border-radius: 8px 0 0 8px;
          padding: 8px 50px;
        }
        &:last-child {
          border-radius: 0 8px 8px 0;
        }
      }
    }
  }
}
.advantages {
  padding: 40px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 45px;
  }
  &__left {
    width: 745px;
    margin-right: 16px;
  }
  &__right {
    width: calc(100% - 761px);
  }
  .section-2_col {
    margin-left: 0;
  }
  &__slider {
    position: relative;
    z-index: 2;
  }
  @media screen and (min-width: 577px) {
    .swiper-wrapper {
      width: 100% !important;
      display: grid;
      grid-template-columns: repeat(2,calc((100% / 2) - (24px * 1 / 2)));
      gap: 24px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
      margin: 0 !important;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    background: #ffffff;
    border: 1px solid #828182;
    border-radius: 1000px;
    &-icon {
      flex-shrink: 0;
      width: 90px;
      img {
        width: 100%;
      }
    }
    &-text {
      padding: 0 12px;
    }
  }
}
.application {
  padding: 40px 0;
  &__items-top {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 32px / 2));
    gap: 24px 32px;
    margin-top: 32px;
    .application__item {
      &:nth-child(1) {
        background: transparent;
        padding: 0;
        align-items: start;
      }
    }
  }
  &__slider {
    margin-top: 32px;
  }
  @media screen and (min-width: 991px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(2,calc((100% / 2) - (32px * 1 / 2)));
      gap: 32px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    background: #F0F7FC;
    border-radius: 12px;
    padding: 24px 16px;
    &-img {
      width: 100%;
      img {
        width: 100%;
        height: 100%;
        border-radius: 12px;
      }
    }
    &-text {
      p:not(:first-child) {
        margin-top: 16px;
      }
    }
    .detailed {
      font-weight: 700;
      color: #025EA1;
      display: none;
      cursor: pointer;
    }
    &-icon {
      flex-shrink: 0;
      width: 90px;
      margin-right: 32px;
      img {
        width: 100%;
      }
    }
  }
  .hide {
    display: block;
  }
}
.milestones {
  padding: 40px 0;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 50px;
    margin-top: 75px;
  }
  &__item {
    display: flex;
    align-items: center;
    &-year {
      min-width: 160px;
      font-size: 48px;
      line-height: 120%;
      font-weight: 700;
      color: #025EA1;
    }
    &-img {
      flex-shrink: 0;
      width: 170px;
      height: 170px;
      margin-left: 183px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: -100px;
        transform: translate(0, -50%);
        width: 100px;
        height: 1px;
        background: #BABABA;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 50%;
        left: -100px;
        transform: translate(0, -50%);
        width: 16px;
        height: 16px;
        background: #FFF;
        border: 5px solid #EEE;
        border-radius: 50px;
      }
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      };
    }
    &-desc {
      max-width: 646px;
      margin-left: 127px;
    }
    &-name {
      color: #025EA1;
    }
    &-text {
      margin-top: 10px;
    }
  }
  &__info {
    background: #025EA1;
    padding: 30px 0 0 0;
    margin-top: 60px;
    &-flex {
      width: calc(100% + 50px);
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }
    &-item {
      max-width: 280px;
      color: #ffffff;
      text-align: center;
      margin: 0 50px 30px 0;
    }
    &-number {
      font-size: 35px;
      line-height: 120%;
    }
    &-text {
      margin-top: 8px;
    }
  }
}
.status {
  padding: 40px 0;
  &__btn {
    width: 350px;
    height: 66px;
    margin-top: 40px;
  }
}
.tour {
  margin: 40px 0;
  &__inner {
    min-height: 210px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ffffff;
    background-image: url("../img/tour-bg.webp");
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    border-radius: 12px;
    padding: 24px;
  }
}
.operator {
  padding: 40px 0 80px;
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 44px;
  }
  &__desc {
    width: calc(100% - 747px);
