    margin-top: 32px;
    &-title {
      font-size: 17.5px;
      line-height: 140%;
      font-weight: 700;
      color: #025EA1;
      text-transform: uppercase;
    }
  }
  &__list {
    margin-top: 40px;
    li {
      list-style: disc;
      list-style-position: inside;
      &:not(:first-child) {
        margin-top: 24px;
      }
    }
  }
  .hide {
    display: block;
  }
  .detailed {
    color: #025EA1;
    display: none;
    cursor: pointer;
  }
  &__btn {
    width: 488px;
    height: 66px;
    margin-top: 40px;
  }
}

