.management {
  padding: 40px 0;
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-top: 40px;
  }
  &__left {
    width: 700px;
    margin-right: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #BABABA;
    border-radius: 12px;
    padding: 30px 15px;
  }
  &__right {
    width: calc(100% - 724px);
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__slider {
    position: relative;
  }
  @media screen and (min-width: 768px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: 100%;
      gap: 24px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  &-slide {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #F0F7FC;
    border-radius: 12px;
    padding: 30px 60px;
    &__text {
      p span {
        font-weight: 700;
        color: #025EA1;
      }
    }
  }
}
.participation {
  padding: 40px 0;
  overflow: hidden;
  &__tabs {
    &-head {
      display: inline-flex;
      align-items: center;
      background: #ffffff;
      margin-top: 40px;
      overflow-x: auto;
      position: relative;
      &::-webkit-scrollbar {
        height: 0;
      }
      &::-webkit-scrollbar-track {
        background: #ffffff;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #000000;
        border-radius: 3px;
      }
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: calc(100% - 100px);
        height: 100%;
        background: #F7F8FA;
        pointer-events: none;
      }
    }
    &-body {
      margin-top: 40px;
    }
  }
  &__tab {
    position: relative;
    z-index: 2;
    height: 60px;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    background: #F7F8FA;
    border-radius: 55px;
    padding: 5px 48px;
    text-transform: uppercase;
    cursor: pointer;
    &--active {
      background: #025EA1;
      color: #ffffff;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__img {
    width: 220px;
    margin-right: 28px;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: calc(100% - 248px);
    min-height: 230px;
    flex-direction: column;
    justify-content: center;
    background: #F0F7FC;
    border-radius: 12px;
    padding: 25px 20px;
    display: none;
    &--active {
      display: flex;
    }
  }
}
.participants {
  padding: 40px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .swiper-button-prev {
    position: static;
    margin-top: 0;
  }
  .swiper-button-next {
    position: static;
    margin-top: 0;
    margin-left: 100px;
  }
  &__slider {
    margin-top: 40px;
    &-nav {
      display: flex;
      align-items: center;
      margin-left: 32px;
    }
  }
  &__swiper-wrapper {
    align-items: stretch;
  }
  &-slide {
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    &__img {
      max-width: 150px;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        max-width: 100%;
      }
      img:nth-child(2) {
        display: none;
      }
      &:hover {
        img:nth-child(1) {
          display: none;
        }
        img:nth-child(2) {
          display: block;
        }
      }
    }
  }
}
.consortium-form {
  padding: 40px 0;
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
  }
  &__img {
    width: 318px;
    margin-right: 30px;
  }
  &__form {
    width: calc(100% - 348px);
    background: #F0F7FC;
    border-radius: 12px;
    padding: 40px;
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 80px / 2));
    gap: 24px 80px;
  }
  &__group {
    margin: 0;
    padding: 0;
    border: none;
    input {
      width: 100%;
      height: 46px;
      background: #ffffff;
      border: 1px solid transparent;
      border-radius: 6px;
      padding: 10px 20px;
      font-size: 16px;
      line-height: 120%;
      color: #003274;
      &:focus {
        border: 1px solid #025EA1;
      }
      &::placeholder {
        color: #025EA1;
      }
    }
    textarea {
      width: 100%;
      height: 100%;
      background: #ffffff;
      border: 1px solid transparent;
      border-radius: 6px;
      padding: 5px 20px;
      font-size: 16px;
      line-height: 120%;
      color: #7E7E7E;
      resize: none;
      &:focus {
        border: 1px solid #025EA1;
      }
      &:focus-visible {
        outline: 0!important;
        outline-color: transparent!important;
        outline-width: 0!important;
        outline-style: none!important;
      }
      &::placeholder {
        color: #003274;
      }
    }
  }
  &__btn {
    width: 180px;
    height: 48px;
    span {
      font-size: 16px;
      line-height: 120%;
    }
  }
  &__info {
    font-size: 12px;
    line-height: 140%;
    margin-top: 24px;
    a {
      text-decoration: underline;
      text-underline-offset: 3px;
      color: #000000;
    }
  }
  .big {
    grid-row: 3/5;
    grid-column: 2/3;
  }
}
.consortium-video {
  padding: 40px 0 80px;
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-top: 40px;
  }
  &__left {
    width: 50%;
    background: #F0F7FC;
    border-radius: 12px;
    padding: 15px 35px;
  }
  &__right {
    display: flex;
    justify-content: center;
    width: 50%;
    border-radius: 12px;
    margin-left: 22px;
    position: relative;
    &2 {
      order: -1;
      margin: 0 22px 0 0;
    }
    video {
      width: 100%;
      height: 100%;
      object-fit: inherit;
    }
    .play {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      justify-content: center;
      align-items: center;
      width: 68px;
      height: 48px;
      background: rgba(33, 33, 33, 0.80);
      border-radius: 12px;
      cursor: pointer;
      &--active {
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  &__text {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    p:not(:first-child) {
      margin-top: 16px;
    }
  }
  .hide {
    display: block;
  }
  .detailed {
    display: none;
  }
  .works__item-link {
    margin-top: 40px;
  }
}


@media screen and (max-width: 1350px) {
  .management {
    &__left {
      width: calc(100% - 586px);
    }
    &__right {
      width: 586px;
    }
  }
  .consortium-form {
    &__grid {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .management {
    &__content {
      display: block;
    }
    &__left {
      max-width: 100%;
      width: 100%;
      margin: 0 0 24px 0;
    }
    &__right {
      width: 100%;
    }
  }
  .consortium-form {
    &__img {
      width: 250px;
      margin-right: 24px;
    }
    &__form {
      width: calc(100% - 274px);
      padding: 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .consortium-form {
    &__img {
      display: none;
    }
    &__form {
      width: 100%;
    }
  }
  .consortium-video {
    &__content {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      order: -1;
      width: 100%;
      height: 400px;
      margin: 0 0 32px 0;
    }
  }
}

@media screen and (max-width: 767px) {
  .management {
    padding: 15px 0;
    &__slider {
      padding: 0 20px;
    }
    &-slide {
      padding: 16px;
    }
    .swiper-button-prev {
      left: -7px;
    }
    .swiper-button-next {
      right: -7px;
    }
  }
  .participation {
    padding: 15px 0;
    &__tabs {
      &-head {
        margin-top: 24px;
      }
      &-body {
        margin-top: 32px;
      }
    }
    &__tab {
      height: 40px;
      padding: 3px 20px;
    }
    &__content {
      flex-direction: column;
    }
    &__img {
      order: -1;
      max-width: 200px;
      width: 100%;
      margin: 0 auto 24px auto;
    }
    &__desc {
      width: 100%;
      padding: 16px;
    }
  }
  .participants {
    padding: 15px 0;
    &-slide {
      &__img {
        max-width: 75px;
      }
    }
    .swiper-button-next {
      margin-left: 42px;
    }
  }
  .consortium-form {
    padding: 15px 0;
    &__form {
      max-width: 500px;
      margin: 0 auto;
      padding: 24px 20px;
    }
    &__grid {
      grid-template-columns: 100%;
      gap: 16px;
    }
    &__group {
      input {
        height: 35px;
        font-size: 14px;
        padding: 3px 15px;
      }
      textarea {
        height: 80px;
        font-size: 14px;
        padding: 10px 15px;
      }
    }
    &__btn {
      width: 132px;
      height: 36px;
      span {
        font-size: 14px;
      }
    }
    &__info {
      margin-top: 16px;
    }
    .big {
      grid-row: unset;
      grid-column: unset;
    }
  }
  .consortium-video {
    padding: 15px 0 30px;
    &__content {
      margin-top: 24px;
    }
    &__left {
      padding: 16px;
    }
    &__right {
      height: 300px;
    }
    .hide {
      display: none;
    }
    .detailed {
      display: block;
    }
    .works__item-link {
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 400px) {
  .consortium-video {
    &__right {
      height: 200px;
    }
  }
}