.footer-bottom {
  background: #025EA1;
  border-top: 1px solid rgba(255, 255, 255, 0.25);
  padding-top: 10px;
  padding-bottom: 56px;
}
@media (max-width: 1220px) {
  .footer-bottom {
    padding-bottom: 20px;
  }
}
.footer-bottom_row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1220px) {
  .footer-bottom_row {
    display: block;
  }
}
.footer-bottom_copy {
  color: #FFF;
  font-size: 11px;
  line-height: 111%;
}
@media (max-width: 1220px) {
  .footer-bottom_copy {
    margin-bottom: 20px;
  }
}
.footer-bottom_list {
  display: flex;
}
@media (max-width: 1220px) {
  .footer-bottom_list {
    display: block;
  }
}
.footer-bottom_list li {
  margin-left: 74px;
}
@media (max-width: 1220px) {
  .footer-bottom_list li {
    margin-left: 0;
    margin-bottom: 20px;
  }
}
.footer-bottom_list li a {
  color: #FFF;
  font-size: 11px;
  line-height: 111%;
  text-decoration-line: underline;
}
.footer-bottom_list li a:hover {
  text-decoration: none;
}
.footer {
  background: #025EA1;
  padding-top: 66px;
  padding-bottom: 91px;
}
@media (max-width: 1220px) {
  .footer {
    padding-bottom: 20px;
  }
}
.footer_logo {
  margin-bottom: 66px;
}
.footer_row {
  display: flex;
}
@media (max-width: 1220px) {
  .footer_row {
    display: block;
  }
}
@media (max-width: 1220px) {
  .footer_col {
    width: 100% !important;
    margin-bottom: 20px;
  }
}
.footer_col:nth-child(1) {
  width: 34%;
}
.footer_col:nth-child(2) {
  width: 28%;
}
.footer_col:nth-child(3) {
  width: 24%;
}
.footer_tel {
  color: #FFF;
  font-size: 18px;
  font-weight: 700;
  line-height: 111%;
  margin-bottom: 20px;
}
.footer_email {
  color: #FFF;
  font-size: 18px;
  line-height: 111%;
  margin-bottom: 20px;
}
.footer_address {
  color: #FFF;
  font-size: 18px;
  line-height: 111%;
}
.footer_list li {
  margin-bottom: 20px;
}
.footer_list li:last-child {
  margin-bottom: 0;
}
.footer_list a {
  color: #FFF;
  font-size: 18px;
  line-height: 111%;
  text-decoration: none;
}
.footer_list a:hover {
  text-decoration: underline;
  font-weight: 700;
}
.footer_text {
  color: #FFF;
  font-size: 18px;
  line-height: 111%;
  text-decoration-line: underline;
  margin-bottom: 10px;
}
.footer_tel-2 {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  color: #025EA1;
  font-size: 18px;
  line-height: 111%;
  padding: 5px 10px;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.25);
  background: #FFF;
}
@media screen and (max-width: 1220px) {
  .footer_tel-2 {
    width: 200px;
  }
}