.press-center__news {
  margin: 65px 0 40px;
  &-inner {
    height: 100%;
    min-height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-image: url("../img/press-center__news-img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 12px;
    color: #ffffff;
    padding: 32px;
  }
  &-title {
    display: flex;
    align-items: center;
    span {
      text-transform: uppercase;
    }
    i {
      margin-left: 12px;
    }
  }
  &-text {
    margin-top: 12px;
  }
}
.news2 {
  padding: 40px 0;
  &__tabs {
    &-head {
      display: inline-flex;
      align-items: center;
      background: #F7F8FA;
      border-radius: 55px;
    }
    &-body {
      margin-top: 55px;
    }
  }
  &__tab {
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F7F8FA;
    border-radius: 55px;
    padding: 5px 48px;
    font-family: FiraSans;
    text-transform: uppercase;
    cursor: pointer;
    &--active {
      background: #025EA1;
      color: #ffffff;
    }
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__slider {
    margin-top: 32px;
    &-nav {
      display: flex;
      align-items: center;
      margin-left: 32px;
    }
  }
  &__content {
    display: none;
    &--active {
      display: block;
    }
  }
  .swiper-slide {
    padding: 0;
  }
  .section-5_button {
    &:after {
      display: none;
    }
  }
  .swiper-button-prev {
    position: static;
  }
  .swiper-button-next {
    position: static;
    margin-left: 100px;
  }
}
.podcasts {
  padding: 40px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__slider {
    margin-top: 32px;
    &-nav {
      display: flex;
      align-items: center;
      margin-left: 32px;
    }
  }
  .swiper-button-prev {
    position: static;
  }
  .swiper-button-next {
    position: static;
    margin-left: 100px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  &-card {
    position: relative;
    height: auto;
    display: flex;
    flex-direction: column;
    border: 1px solid rgba(54, 54, 54, 0.15);
    border-radius: 12px;
    &__video {
     position: relative;
      padding-bottom: 65%;
      video {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        border-radius: 12px 12px 0 0;
      }
    }
    &__desc {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      border-radius: 0 0 12px 12px;
      padding: 48px 10px 24px 10px;
    }
    &__text {
      margin-bottom: auto;
    }
    &__btn {
      width: 173px;
      height: 65px;
      background: transparent;
      border: 1px solid #333;
      border-radius: 10px;
      margin: 32px 0 0 auto;
    }
  }
}
.press-center__contacts {
  padding: 40px 0 80px;
  &__inner {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
  }
  &-items {
    background: #F0F7FC;
    border-radius: 12px;
    padding: 24px;
    margin-top: 24px;
  }
  &-item {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-top: 24px;
    }
    i {
      flex-shrink: 0;
      width: 24px;
      margin-right: 8px;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .podcasts {
    &-card {
      &__desc {
        padding: 24px 10px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .press-center__news {
    margin: 15px 0;
    &-inner {
      min-height: 300px;
    }
    &-text {
      display: none;
    }
  }
  .news2 {
    padding: 15px 0;
    &__tabs {
      &-body {
        margin-top: 32px;
      }
    }
    &__tab {
      height: 40px;
      font-size: 14px;
      padding: 3px 16px;
    }
    .swiper-button-next {
      margin-left: 42px;
    }
  }
  .podcasts {
    padding: 15px 0;
    &-card {
      &__btn {
        width: 100px;
        height: 40px;
      }
    }
    .swiper-button-next {
      margin-left: 42px;
    }
  }
  .press-center__contacts {
    padding: 15px 0 30px;
    &__inner {
      grid-template-columns: 100%;
      gap: 32px;
    }
  }
}

@media screen and (max-width: 576px) {
  .press-center__news {
    &-inner {
      padding: 16px 10px;
    }
  }
}

@media screen and (max-width: 400px) {
  .press-center__news {
    &-inner {
      min-height: 100%;
      padding: 20px 10px;
    }
    &-title {
      span {
        font-size: 14px;
      }
      i {
        display: none;
      }
    }
  }
}
