.nav {
  padding: 50px 0;
  li {
    font-size: 20px;
    line-height: 150%;
    &:not(:first-child) {
      margin-top: 15px;
    }
    a {
      color: #000000;
      &:hover {
        opacity: 0.6;
      }
    }
  }
}
.text14 {
  font-size: 14px;
  line-height: 140%;
}
.text16 {
  font-size: 16px;
  line-height: 140%;
}
.text18 {
  font-size: 18px;
  line-height: 140%;
}
.text21 {
  font-size: 21px;
  line-height: 140%;
}
.text24 {
  font-size: 24px;
  line-height: 140%;
}
.text35 {
  font-family: FiraSans;
  font-size: 35px;
  line-height: 140%;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  padding-bottom: 19px;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 80px;
    height: 10px;
    background: #025EA1;;
    border-radius: 7px;
  }
}
.text40 {
  font-size: 40px;
  line-height: 140%;
  text-transform: uppercase;
}
.page-top {
  font-family: FiraSans;
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  color: #ffffff;
  position: relative;
  &__inner {
    min-height: 225px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__desc {
    padding: 45px 0;
  }
  &__title {
    font-weight: 400;
  }
  &__text {
    max-width: 860px;
    margin-top: 16px;
    p {
      &:not(:first-child) {
        margin-top: 16px;
      }
    }
  }
}
.tabs {
  padding: 80px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: calc(100% - 570px);
  }
  &__right {
    width: 710px;
    margin-left: 60px;
    margin-right: -200px;
    &2 {
      width: 510px;
      margin-right: 0;
    }
    &3 {
      width: 610px;
      margin-right: -100px;
    }
    figure {
      width: 100%;
    }
    img {
      width: 100%;
    }
  }
  &__btns {
    display: inline-flex;
    align-items: center;
    background: #F7F8FA;
    border-radius: 55px;
    margin-bottom: 32px;
  }
  &__btn {
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F7F8FA;
    border-radius: 55px;
    padding: 5px 48px;
    font-family: FiraSans;
    font-weight: 700;
    text-transform: uppercase;
    cursor: pointer;
    &--active {
      background: #025EA1;
      color: #ffffff;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
  }
  &__desc {
    min-height: 300px;
    flex-direction: column;
   justify-content: center;
    background: #F0F7FC;
    border-radius: 12px;
    padding: 38px 45px;
    display: none;
    &--active {
      display: flex;
    }
  }
  &__nav {
    &-title2 {
      font-weight: 700;
      color: #025EA1;
      span {
        text-transform: uppercase;
      }
    }
  }
  &__list {
    margin-top: 16px;
    li {
      list-style: disc inside;
      padding-left: 8px;
      &:not(:first-child) {
        margin-top: 16px;
      }
    }
  }
  &__text {
    p:not(:first-child) {
      margin-top: 16px;
    }
  }
}
.swiper-button-prev {
  width: 24px;
  margin-top: 0;
  svg {
    width: 100%;
    height: auto;
  }
  &:after {
    display: none;
  }
}
.swiper-button-next {
  width: 24px;
  margin-top: 0;
  svg {
    width: 100%;
    height: auto;
  }
  &:after {
    display: none;
  }
}
.btn-blue {
  background: #025EA1;
  border-radius: 10px;
  span {
    color: #ffffff;
    text-transform: uppercase;
  }
  &:hover {
    background: #004f86;
  }
}
.up {
  position: fixed;
  z-index: 10;
  right: 20px;
  bottom: 20px;
  z-index: 2;
  width: 37px;
  height: 37px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 1px solid #333333;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: all 0.5s;
  &--active {
    opacity: 1;
    pointer-events: all;
  }
}
.nice-select {
  width: 215px;
  height: 40px;
  background: #ffffff;
  border: 1px solid #7E7E7E;
  border-radius: 8px;
  padding: 3px 54px 3px 16px;
  font-size: unset;
  line-height: unset;
  display: flex;
  align-items: center;
  position: relative;
  &:after {
    top: 50%;
    right: 10px;
    transform: translate(0, -50%);
    width: 16px;
    height: 10px;
    background-image: url("../img/arrow-select.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 16px 10px;
  }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 38px;
    height: 100%;
    background: #F7F8FA;
    border-radius: 0 8px 8px 0;
    pointer-events: none;
  }
  &:hover {
    border: 1px solid #7E7E7E;
  }
  .current {
    font-family: FiraSans;
    font-size: 16px;
    line-height: 120%;
    font-weight: 400;
  }
  .list {
    li {
      font-family: FiraSans;
      font-size: 14px;
      line-height: 120%;
      font-weight: 400;
      padding: 10px 16px;
    }
  }
}
.nice-select.open:after {
  transform: translate(0, -50%) rotate(0);
}
.play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 68px;
  height: 48px;
  background: rgba(33, 33, 33, 0.80);
  border-radius: 12px;
  cursor: pointer;
  &--active {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
}
.detailed {
  font-weight: 700;
  color: #025EA1;
  cursor: pointer;
  &--active {
    display: none !important;
  }
}
.hide {
  display: none;
  &--active {
    display: block !important;
  }
}
.hide-btn {
  font-weight: 700;
  color: #025EA1;
  cursor: pointer;
  display: none;
  &--active {
    display: block;
  }
}
.modal {
  background: rgba(80, 80, 80, 0.66);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 1440px;
    max-width: 100%;
    position: relative;
    background: #F0F7FC;
    border-radius: 12px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #025EA1;
    border-radius: 12px 12px 0 0;
    padding: 24px 60px;
  }
  &__title {
    font-weight: 700;
    color: #ffffff;
    text-transform: uppercase
  }
  &__close {
    width: 20px;
    background: transparent;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
      path {
        fill: #ffffff;
        stroke: #ffffff;
      }
    }
  }
  &__content {
    padding: 60px;
  }
  &__name {
    color: #025EA1;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__post {
    color: #025EA1;
    font-weight: 700;
    text-transform: uppercase;
    margin-top: 24px;
  }
  &__text {
    margin-top: 40px;
    p:not(:first-child) {
      margin-top: 5px;
    }
  }
}
.margin-top {
  margin-top: 84px;
}
.margin-top2 {
  margin-top: 102px;
}
.margin-top3 {
  margin-top: 145px;
}

@media screen and (max-width: 1220px) {
  .margin-top2 {
    margin-top: 84px;
  }
  .margin-top3 {
    margin-top: 84px;
  }
}

@media screen and (max-width: 1200px) {
  .tabs {
    &__left {
      width: calc(100% - 430px);
    }
    &__right {
      width: 500px;
      margin-left: 30px;
      margin-right: -100px;
      &2 {
        width: 400px;
        margin-right: 0;
      }
      &3 {
        width: 400px;
        margin-right: 0;
      }
    }
    &__desc {
      padding: 30px 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .tabs {
    &__inner {
      display: block;
    }
    &__content {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      max-width: 500px;
      width: 100%;
      margin: 24px auto 0;
      &3 {
        figure {
          margin-left: 0 !important;
        }
      }
      figure {
        margin-left: 100px;
      }
    }
  }
  .modal {
    &__top {
      padding: 24px;
    }
    &__content {
      padding: 40px 24px;
    }
  }
}

@media screen and (max-width: 767px) {
  .text16 {
    font-size: 14px;
  }
  .text18 {
    font-size: 14px;
  }
  .text21 {
    font-size: 16px;
  }
  .text24 {
    font-size: 18px;
  }
  .text35 {
    font-size: 20px;
    padding-bottom: 8px;
    &:after {
      width: 32px;
      height: 3.8px;
    }
  }
  .text40 {
    font-size: 24px;
  }
  .tabs {
    padding: 30px 0 15px;
    &__right {
      max-width: 400px;
      figure {
        margin-left: 50px;
      }
    }
    &__btn {
      height: 40px;
      font-weight: 400;
      padding: 3px 16px;
    }
    &__desc {
      min-height: 100%;
      padding: 16px;
    }
  }
  .swiper-button-prev {
    width: 12px;
  }
  .swiper-button-next {
    width: 12px;
  }
  .nice-select {
    .current {
      font-size: 14px;
    }
  }
  .modal {
    &__top {
      padding: 24px 16px;
    }
    &__content {
      padding: 32px 16px;
    }
    &__post {
      margin-top: 16px;
    }
    &__text {
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 576px) {
  .text35 {
    font-size: 18px;
  }
  .margin-top {
    margin-top: 54px;
  }
  .margin-top2 {
    margin-top: 54px;
  }
  .margin-top3 {
    margin-top: 54px;
  }
}

@media screen and (max-width: 400px) {
  .text21 {
    font-size: 14px;
  }
  .page-top {
    &__inner {
      min-height: 112px;
      padding: 25px 0;
    }
    &__text {
      margin-top: 5px;
      p {
        font-size: 9px;
      }
    }
  }
  .modal {
    &__top {
      padding: 16px 8px;
    }
    &__content {
      padding: 16px 8px;
    }
    &__text {
      p {
        font-size: 12px !important;
      }
    }
  }
}