.chairman {
  padding: 40px 0;
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
  }
  &__img {
    width: 520px;
    margin-right: 90px;
    img {
      width: 100%;
      border-radius: 12px;
    }
  }
  &__desc {
    width: calc(100% - 610px);
    background: #F0F7FC;
    border-radius: 12px;
    padding: 24px;
  }
  &__name {
    font-size: 35px;
    line-height: 140%;
    font-weight: 700;
    color: #025EA1;
    text-transform: uppercase;
    margin-bottom: 16px;
  }
  &__group {
    &:not(:first-child) {
      margin-top: 16px;
    }
  }
  &__subtitle {
    font-weight: 700;
    color: #025EA1;
    text-transform: uppercase;
    margin-bottom: 16px;
  }
  &__list {
    li {
      list-style: disc inside;
    }
  }
  .hide {
    .chairman__list li {
      &:not(:first-child) {
        margin-top: 16px;
      }
    }
  }
  .detailed {
    margin-top: 16px;
  }
  .hide-btn {
    margin-top: 16px;
  }
  .text16 {
    line-height: 170%;
  }
}
.committees {
  padding: 40px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__slider {
    margin-top: 40px;
  }
  @media screen and (min-width: 768px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(3,calc((100% / 3) - (32px * 2 / 3)));
      gap: 24px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 64px / 3));
    gap: 32px;
    margin-top: 40px;
  }
  &__item {
    background: #F0F7FC;
    border-radius: 12px;
    padding: 24px;
    &-title {
      font-weight: 700;
      color: #025EA1;
      text-transform: uppercase;
    }
    &-subtitle {
      font-weight: 700;
      text-transform: uppercase;
      margin-top: 16px;
    }
    &-list {
      margin-top: 16px;
      li {
        list-style: disc inside;
        &:not(:first-child) {
          margin-top: 8px;
        }
      }
    }
    &-list2 {
     li {
       display: flex;
       justify-content: space-between;
       align-items: center;
       span:nth-child(2) {
         margin-left: 24px;
       }
     }
    }
  }
}
.publications {
  padding: 40px 0 80px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__slider {
    margin-top: 40px;
  }
  @media screen and (min-width: 768px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(2,calc((100% / 2) - (24px * 1 / 2)));
      gap: 24px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  .works__content {
    margin-top: 60px;
  }
  &__btn {
    width: 285px;
    height: 65px;
    margin-top: 60px;
  }
}


@media screen and (max-width: 1300px) {
  .chairman {
    &__img {
      width: 480px;
      margin-right: 30px;
    }
    &__desc {
      width: calc(100% - 510px);
    }
  }
}

@media screen and (max-width: 1200px) {
  .chairman {
    &__img {
      width: 430px;
      margin-right: 30px;
    }
    &__desc {
      width: calc(100% - 460px);
    }
  }
  .committees {
    .swiper-wrapper {
      grid-template-columns: repeat(2, calc(100% / 2 - 24px / 2));
      gap: 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .chairman {
    &__content {
      display: block;
    }
    &__img {
      max-width: 500px;
      width: 100%;
      margin: 0 auto 24px auto;
    }
    &__desc {
      width: 100%;
    }
  }
}

@media screen and (max-width: 767px) {
  .chairman {
    &__desc {
      padding: 16px;
    }
    &__name {
      font-size: 18px;
    }
  }
  .committees {
    padding: 15px 0;
    &__slider {
      margin-top: 24px;
      &-nav {
        display: flex;
        align-items: center;
        margin-left: 32px;
      }
    }
    .swiper-wrapper {
      align-items: stretch;
      gap: unset;
    }
    &__item {
      height: auto;
      padding: 16px;
    }
    .swiper-button-prev {
      position: static;
    }
    .swiper-button-next {
      position: static;
      margin-left: 42px;
    }
  }
  .publications {
    padding: 15px 0 30px;
    &__slider {
      &-nav {
        display: flex;
        align-items: center;
        margin-left: 32px;
      }
    }
    .swiper-wrapper {
      align-items: stretch;
      gap: unset;
    }
    .swiper-slide {
      height: auto;
    }
    .swiper-button-prev {
      position: static;
    }
    .swiper-button-next {
      position: static;
      margin-left: 42px;
    }
    &__btn {
      width: 186px;
      height: 42px;
      margin-top: 32px;
    }
  }
}