.contacts {
  padding: 65px 0;
  &__content {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 40px / 2));
    gap: 40px;
  }
  &__left {
    display: flex;
    flex-direction: column;
  }
  &__right {
    display: flex;
    flex-direction: column;
  }
  &__item {
    flex-grow: 1;
    background: #F0F7FC;
    border-radius: 12px;
    padding: 24px;
    margin-top: 32px;
  }
  &__connection {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    &-item {
      display: flex;
      align-items: center;
      i {
        flex-shrink: 0;
        width: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
        svg {
          width: 100%;
          height: auto;
        }
      }
      span {
        margin-left: 8px;
      }
    }
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 32px;
    &-group {
      padding: 0;
      margin: 0;
      input {
        color: #000000;
        &::placeholder {
          color: #7E7E7E;
        }
      }
    }
    &-btn {
      width: 251px;
      height: 44px;
    }
    &-info {
      max-width: 465px;
      font-size: 12px;
      line-height: 140%;
      a {
        text-decoration: underline;
        text-underline-offset: 3px;
      }
    }
  }
  &__text {
    p {
      &:not(:first-child) {
        margin-top: 8px;
      }
      span:nth-child(1) {
        font-weight: 700;
        color: #7E7E7E;
      }
    }
  }
  .map {
    width: 100%;
    height: 390px;
    margin-top: 40px;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    &__content {
      grid-template-columns: 100%;
      gap: 32px;
    }
  }
}

@media screen and (max-width: 767px) {
  .contacts {
    padding: 30px 0;
    &__connection {
      gap: 16px;
    }
    &__item {
      margin-top: 24px;
      padding: 16px;
    }
    &__form {
      margin-top: 24px;
      &-btn {
        height: 35px;
      }
    }
    .map {
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 400px) {
  .contacts {
    .map {
      height: 200px;
    }
  }
}