.mediabank {
  padding: 65px 0;
  &__tabs {
    &-head {
      display: inline-flex;
      align-items: center;
      background: #F7F8FA;
      border-radius: 55px;
    }
    &-body {
      margin-top: 32px;
    }
  }
  &__tab {
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F7F8FA;
    border-radius: 55px;
    padding: 5px 48px;
    font-family: FiraSans;
    text-transform: uppercase;
    cursor: pointer;
    &--active {
      background: #025EA1;
      color: #ffffff;
    }
  }
  &__status {
    font-weight: 700;
    color: #025EA1;
    text-transform: uppercase;
  }
  &__cards {
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    grid-auto-rows: 1fr;
    gap: 30px;
    margin-top: 32px;
    display: none;
    &--active {
      display: grid;
    }
  }
  &__card {
    &-img {
      position: relative;
      padding-bottom: 65%;
     img {
       position: absolute;
       top: 0;
       left: 0;
       right: 0;
       bottom: 0;
       width: 100%;
       height: 100%;
       border-radius: 12px;
     }
    }
    &-video {
      width: 100%;
      border-radius: 12px;
      position: relative;
      padding-bottom: 65%;
      video {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        border-radius: 12px;
      }
    }
    &-date {
      color: #025EA1;
      font-weight: 700;
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 767px) {
  .mediabank {
    padding: 30px 0;
    &__cards {
      grid-template-columns: 100%;
      gap: 24px;
    }
    &__tab {
      height: 40px;
      font-weight: 400;
      padding: 3px 32px;
    }
  }
}