.works {
  padding: 65px 0;
  &__tabs {
    &-head {
      display: inline-flex;
      align-items: center;
      background: #F7F8FA;
      border-radius: 55px;
    }
    &-body {
      margin-top: 60px;
    }
  }
  &__tab {
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F7F8FA;
    border-radius: 55px;
    padding: 5px 48px;
    font-family: FiraSans;
    text-transform: uppercase;
    cursor: pointer;
    &--active {
      background: #025EA1;
      color: #ffffff;
    }
  }
  &__content {
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    display: none;
    &--active {
      display: grid;
    }
  }
  &__item {
    &-title {
      font-weight: 700;
      color: #025EA1;
      text-transform: uppercase;
    }
    &-text {
      text-transform: uppercase;
      margin-top: 30px;
    }
    &-link {
      display: flex;
      align-items: center;
      margin-top: 24px;
      i {
        width: 48px;
        img {
          width: 100%;
        }
      }
      span {
        font-weight: 700;
        color: #025EA1;
        margin-left: 50px;
      }
    }
  }
  &__card {
    display: flex;
    flex-direction: column;
    &-title {
      font-weight: 700;
      color: #025EA1;
      text-transform: uppercase;
      margin-bottom: auto;
    }
    &-text {
      margin-top: 24px;
      margin-bottom: auto;
    }
    &-img {
      margin-top: 20px;
      img {
        width: 100%;
      }
    }
    &-btn {
      width: 228px;
      height: 65px;
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 767px) {
  .works {
    padding: 30px 0;
    &__tabs {
      &-body {
        margin-top: 40px;
      }
    }
    &__tab {
      height: 40px;
      font-size: 14px;
      font-weight: 400;
      padding: 3px 16px;
    }
    &__content {
      grid-template-columns: 100%;
    }
    &__item {
      &-link {
        i {
          width: 32px;
        }
        span {
          margin-left: 16px;
        }
      }
    }
    &__card {
      &-btn {
        width: 186px;
        height: 42px;
      }
    }
  }
}