.guide {
  padding: 40px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__slider {
    margin-top: 50px;
    &-nav {
      display: flex;
      align-items: center;
    }
  }
  .swiper-button-prev {
    position: static;
  }
  .swiper-button-next {
    position: static;
    margin-left: 100px;
  }
  &-card {
    cursor: pointer;
    &:hover {
      .guide-card__top {
        &:after {
          opacity: 1;
          pointer-events: all;
        }
      }
      .guide-card__hover {
        opacity: 1;
        pointer-events: all;
      }
    }
    &__top {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.70);
        border-radius: 12px;
        opacity: 0;
        pointer-events: none;
        transition: all 0.5s;
      }
    }
    &__img {
      padding-bottom: 127%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        border-radius: 12px;
      }
    }
    &__desc {
      text-align: center;
      margin-top: 24px;
    }
    &__title {
      font-weight: 700;
      color: #025EA1;
      text-transform: uppercase;
    }
    &__name {
      font-weight: 700;
      text-transform: uppercase;
      margin-top: 24px;
    }
    &__hover {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 24px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
      &-desc {
        color: #FFF;
        p:not(:first-child) {
          margin-top: 24px;
        }
      }
      &-btn {
        flex-shrink: 0;
        width: 159px;
        height: 62px;
        background: transparent;
        border: 1px solid #FFF;
        border-radius: 10px;
        margin-top: 24px;
        span {
          font-weight: 700;
          color: #ffffff;
        }
        &:hover {
          background: #ffffff;
          span {
            color: #000000;
          }
        }
      }
    }
  }
}
.info {
  padding: 40px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__slider {
    max-width: 1300px;
    margin: 50px auto 0;
    &-nav {
      display: flex;
      align-items: center;
      margin-left: 32px;
    }
  }
  .swiper-slide {
    img {
      width: 100%;
    }
  }
  .swiper-button-prev {
    position: static;
  }
  .swiper-button-next {
    position: static;
    margin-left: 100px;
  }
  &-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #F0F7FC;
    border-radius: 12px;
    padding: 36px 48px;
    margin-top: 120px;
    &__title {
      font-size: 35px;
      line-height: 140%;
    }
    &__tel {
      display: flex;
      align-items: center;
      font-size: 26px;
      line-height: 140%;
      margin-top: 8px;
    }
    &__btn {
      width: 320px;
      height: 66px;
      margin-left: 32px;
    }
  }
}
.purchases {
  padding: 40px 0;
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
  }
  &__left {
    width: 320px;
    margin-right: 40px;
  }
  &__right {
    width: calc(100% - 360px);
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__btn {
    width: 100%;
    height: 66px;
    margin-top: 30px;
  }
  &__desc {
    background: #F0F7FC;
    border-radius: 12px;
    padding: 47px 79px;
    p:not(:first-child) {
      margin-top: 24px;
    }
  }
  .hide {
    display: block;
  }
  .detailed {
    display: none;
  }
}
.requisites {
  padding: 40px 0 80px 0;
  &__desc {
    margin-top: 50px;
  }
  &__desc {
    background: #F0F7FC;
    border-radius: 12px;
    padding: 40px 24px;
    p {
      &:not(:first-child) {
        margin-top: 8px;
      }
      span:nth-child(1) {
        font-weight: 700;
        color: #7E7E7E;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .purchases {
    &__desc {
      padding: 48px 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .guide {
    &-card {
      &__hover {
        padding: 8px;
        &-desc {
          p {
            font-size: 14px !important;
          }
          p:not(:first-child) {
            margin-top: 16px;
          }
        }
        &-btn {
          width: 115px;
          height: 42px;
          span {
            font-size: 14px !important;
          }
        }
      }
    }
  }
  .info {
    &-bottom {
      display: block;
      &__btn {
        margin: 16px 0 0 0;
      }
    }
  }
  .purchases {
    &__left {
      width: 250px;
      margin-right: 24px;
    }
    &__right {
     width: calc(100% - 274px);
    }
  }
}

@media screen and (max-width: 767px) {
  .guide {
    padding: 15px 0;
    &__slider {
      max-width: 400px;
      margin: 24px auto 0;
    }
    .swiper-button-next {
      margin-left: 42px;
    }
  }
  .info {
    padding: 15px 0;
    &__slider {
      margin-top: 24px;
    }
    .swiper-button-next {
      margin-left: 42px;
    }
    &-bottom {
      padding: 16px;
      margin-top: 32px;
      &__title {
        font-size: 21px;
      }
      &__tel {
        font-size: 16px;
      }
      &__btn {
        width: 186px;
        height: 42px;
      }
    }
  }
  .purchases {
    padding: 15px 0;
    &__content {
      margin-top: 24px;
    }
    &__left {
      display: none;
    }
    &__right {
      width: 100%;
    }
    &__desc {
      padding: 21px 16px;
    }
    .hide {
      display: none;
    }
    .detailed {
      display: block;
    }
  }
  .requisites {
    padding: 15px 0 30px;
    &__desc {
      padding: 21px 16px;
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 576px) {
  .info {
    &__slider {
      max-width: 300px;
    }
  }
}

@media screen and (max-width: 400px) {
  .guide {
    &-card {
      &__hover {
        &-desc {
          p {
            font-size: 12px !important;
          }
        }
      }
      &__desc {
        margin-top: 16px;
      }
      &__name {
        margin-top: 16px;
      }
    }
  }
}